body {
  background-color: #1e1e2f;
  justify-content: center;
  margin: 0;
  display: flex;
}

.label {
  color: #fff;
}

.timer {
  margin-top: 300px;
  display: flex;
}

.field {
  text-align: center;
}

.value {
  padding-left: 30px;
  padding-right: 30px;
  font-size: 90px;
  font-weight: bold;
  display: block;
}

.field:first-child .value {
  color: #e655c6;
}

.field:nth-child(2) .value {
  color: #dd490f;
}

.field:nth-child(3) .value {
  color: #61d415;
}

.field:nth-child(4) .value {
  color: #10b8d6;
}
